@import url(https://fonts.googleapis.com/css?family=Work+Sans);
body {
  margin: 0;
  font-family: 'Work Sans';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #eceff1;
  color: #4d4d4d;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Digicard {
  width: 240px;
  height: 310px;
  padding: 10px;
  margin: 1rem;
  box-shadow: 7px 10px 12px -5px rgba(0,0,0,0.56);
  text-align: center;
  background-color: white;
  border-radius: 5px;
}

.Digicard-title {
  margin-bottom: 1rem;
  font-weight: bold;
  font-size: 150%; 
}

.char-img {
  width: 180px;
  transition: all ease 500ms
}

.char-img:hover {
  transform: scale(1.25);
  opacity: 0.8;
}

.Digicard-data {
  font-size: 80%;
}

.Digicard-image {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 190px;
  background-color: #ffffff;
  border-radius: 3px;
  margin-bottom: 1rem;
}
.Digidex {
  text-align: center;
}

.Digidex-cards {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  padding: 10px;
  margin: 20px;
}

.Digidex-winner{
  color: #4caf50
}

.Digidex-loser {
  color: #e91e63
}

